.photos-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  row-gap: 24px;
}
.photos-list .photos-list__item {
  padding-bottom: 29px;
  position: relative;
}
.photos-list .photos-list__title {
  padding-top: 15px;
  padding-bottom: 5px;
}
.photos-list .photos-list__author {
  padding-bottom: 16px;
}
.photos-list .descrip-block__likes-total {
  display: flex;
  align-items: center;
  column-gap: 14px;
}
.photos-list .descrip-block__likes-total .descrip-block__button {
  background: none;
  border: none;
  padding: 0;
  display: block;
  cursor: pointer;
}
.photos-list .descrip-block__likes-total .descrip-block__button::before {
  content: url("../img/catalog/like-icon.svg");
  display: block;
  width: 23px;
  height: 23px;
}
.photos-list .photos-list-place {
  grid-area: place;
}
.photos-list .photos-list-flowers {
  grid-area: flowers;
}
.photos-list .photos-list-north {
  grid-area: north;
}
.photos-list .photos-list-chert {
  grid-area: chert;
}
.photos-list .photos-list__item::after {
  content: "";
  position: relative;
}
@media (min-width: 768px) {
  .photos-list {
    width: 648px;
    margin: 0 auto;
    display: grid;
    grid-template-areas: "place place" "flowers north" "chert .";
    row-gap: 0;
  }
  .photos-list .photos-list__image-block {
    display: flex;
  }
  .photos-list .photos-list__item {
    padding-bottom: 0;
  }
  .photos-list .photos-list__title {
    padding: 0;
    padding-bottom: 5px;
  }
  .photos-list .photos-list__author {
    padding: 0;
  }
  .photos-list .photos-list__descrip-block {
    display: flex;
    background-color: #eeeeee;
    padding-top: 22px;
    padding-bottom: 15px;
    padding-left: 20px;
    padding-right: 20px;
    justify-content: space-between;
  }
  .photos-list .descrip-block__likes-total {
    column-gap: 9px;
  }
  .photos-list .descrip-block__likes-total .descrip-block__button {
    background: none;
    border: none;
    padding: 0;
    display: block;
    cursor: pointer;
  }
  .photos-list .descrip-block__likes-total .descrip-block__button::before {
    content: url("../img/catalog/like-icon.svg");
    display: block;
    width: 23px;
    height: 23px;
  }
}
@media (min-width: 1280px) {
  .photos-list {
    min-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-areas: "place place place" "flowers north chert";
    row-gap: 0;
  }
  .photos-list .photos-list__image-block {
    display: flex;
  }
  .photos-list .photos-list__item {
    padding-bottom: 0;
    position: relative;
  }
  .photos-list .photos-list__title {
    padding: 0;
    padding-bottom: 5px;
  }
  .photos-list .photos-list__author {
    padding: 0;
    color: #ffffff;
  }
  .photos-list .photos-list__descrip-block {
    background: rgba(0, 0, 0, 0.4);
    padding-top: 22px;
    padding-bottom: 15px;
    padding-left: 20px;
    padding-right: 20px;
    position: absolute;
    display: flex;
    justify-content: space-between;
    bottom: 0;
    color: #ffffff;
  }
  .photos-list .descrip-block {
    width: 100%;
    box-sizing: border-box;
  }
  .photos-list .descrip-block__likes-total {
    column-gap: 9px;
  }
  .photos-list .descrip-block__likes-total .descrip-block__button {
    background: none;
    border: none;
    padding: 0;
    display: block;
    cursor: pointer;
  }
  .photos-list .descrip-block__likes-total .descrip-block__button::before {
    content: url("../img/catalog/like-icon-desktop.svg");
    display: block;
    width: 23px;
    height: 23px;
  }
}
